import { Models } from '../server'
import React, { Component } from 'react';

export class Timer extends Component {

    state = {
        secondsLeft: 0
    }

    async componentDidMount(){
        const { Timer } = Models
        Timer.remaining()
            .then( secondsLeft => {
                this.setState({ secondsLeft  })
            })
    }

    render(){
        var time = this.state.secondsLeft/1000
        if(time < 0) return <span className="text-danger">Game Over</span>
        var minutes = Math.floor(time / 60);  
        var seconds = time - minutes * 60;
        return `Time Remaining: ${minutes}:${seconds.toFixed()}`
    }

}